.wp-block-blocks-gamestore-block-services {
	max-width: 100vw !important;
	margin-right: calc(var(--wp--style--root--padding-right) * -1) !important;
	margin-left: calc(var(--wp--style--root--padding-left) * -1) !important;
	margin-top: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem) !important;
	text-align: center;
	position: relative;
	background-size: cover;

	.reverse {
		flex-direction: row-reverse;
	}
	button {
		margin-right: auto;
		margin-top: 20px;
	}
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(180deg, var(--background-default) 0%, var(--background-80) 50.06%, var(--background-default) 100%);
		opacity: 1;
		z-index: 0;
	}

	.cta-inner {
		text-align: left;
		border-radius: 8px;
		background: var(--background-2, rgba(250, 250, 250, 0.02));
		backdrop-filter: blur(12px);
		display: flex;
		align-items: stretch;
		gap: 30px;
		position: relative;
		justify-content: space-between;

		.left-part {
			display: flex;
			flex-direction: column;
			gap: 16px;
			padding: 28px;
			max-width: 930px;
			p {
					line-height: 2!important;
				}
			.cta-title {
				margin: 0;

				span {
					background: var(--accent-gradient);
					background-clip: text;
					-webkit-background-clip: text;
					-webkit-text-fill-color: transparent;
				}
				
			}

			.cta-description {
				margin: 0;
				color: var(--text-secondary, rgba(14, 13, 15, 0.70));
			}

			.links-list {
				margin-top: 16px;
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				gap: 24px;
				a {
					border-radius: 5px;
					border: 1px solid var(--divider-main, rgba(14, 13, 15, 0.08));
					display: inline-block;
					font-size: 14px;
					font-weight: 700;
					line-height: 140%;
					letter-spacing: 2.1px;
					text-transform: uppercase;
					text-decoration: none;
					color: var(--text-primary);
					padding: 20px 32px;
				}
			}
		}

		.right-part {

			
		}
	}
}